import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { DomSanitizer } from '@angular/platform-browser';
import { FileDownloadService } from 'src/app/shared/services/file-download.service';
import { DocumentService } from 'src/app/api/controllers/Document';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { MatDialog } from '@angular/material/dialog';
import { DocumentCreateComponent } from '../document-config/document-create/document-create.component';

@Component({
  selector: 'app-my-document-user-list',
  templateUrl: './my-document-user-list.component.html',
  styleUrls: ['./my-document-user-list.component.scss']
})
export class MyDocumentUserListComponent implements OnInit {
  isDataLoaded:boolean;
  documentViewList:any = [];
  totalItems:number;
  constructor(
    private sessionService:SessionService,
    private documentService:DocumentService,
    private fileDownloadService: FileDownloadService,
    private sanitizer: DomSanitizer,
    public dialog: MatDialog,
    private changeDetection:ChangeDetectorRef
  ) { }

  getDateFormat(date) {
    return moment(date).format('ddd MMM D YYYY hh:mm A');
  }

  openUploadModal() {
    const dialogRef = this.dialog.open(DocumentCreateComponent, {
      panelClass: 'material-dialog-medium',
      data: {from:'private',apartmentBlockUnitId:this.sessionService.apartmentBlockUnitID}
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.getDocumetList();
      }
    });
  }

  downloadFile(data,url) {
    if(data.filePath && !data.fileUrl) {
      this.fileDownloadService.downloadFile(data.filePath).subscribe((down:any) => {
        const blob = down.body;
        let objectURL,sanitizeUrl:any,trigger;
        objectURL = URL.createObjectURL(blob); 
        sanitizeUrl = this.sanitizer.bypassSecurityTrustUrl(objectURL);
        data.fileUrl = sanitizeUrl;
        this.changeDetection.detectChanges();
        trigger = url.firstElementChild;
        trigger.click();
      })
    }
  }

  getDocumetList() {
    this.isDataLoaded = false;
    let params = {
      ApartmentBlockUnitId : this.sessionService.apartmentBlockUnitID,
      ApartmentId : this.sessionService.apartmentId
    }
    this.documentService.getAllDocByApartmentBlockUnitId(params).subscribe((res:any)=>{
      this.isDataLoaded = true;
      if(res.length > 0) {
        this.documentViewList = res;
      }
      this.totalItems = res.length;
    })
  }

  ngOnInit(): void {
    this.getDocumetList();
  }

}
